<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="false" :listType="[DetailListType]" ModuleName="Approval"
            SubModuleCode="Approval" IdentityColumn="GROUPID" :HeaderText="$t('TimesheetApprovalsHistory')"
            :ListData="ApplicationCategoryHistoryList" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'Comment'">
                    <a class="comment" href="javascript:;" :weekdatarange="data.row.TIME_SHEET_PERIOD" userid="63221" title="" data-toggle="modal" data-target="#tsacomment" data-toggle-tooltip="tooltip" data-original-title="Comments(0)"  v-on:click="OpenCommentModal(data.row.TIME_SHEET_PERIOD)">
                        <i class="fa fa-comment-o text-dark action-icon"></i>
                        <span class="noti-circle noti-blue text-center">{{data.row.COMMENTSCOUNT}}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'RollBack'">
                  <a  v-on:click="OpenRollBackModal(data.row)"><i class="fa fa-reply text-info action-icon"></i></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                    <span >{{data.row.STATUS ==  'TRACK_ENTRY_REJECTED'? 'Rejected' : 'Approved' }} </span>
                </template>
            </template>
        </tg-list>
        <ShowComment v-if="ShowCommentModal" :TimeSheetDate="TimeSheetDate"></ShowComment>
        <RollBack v-if="ShowRollBackModal" :TimeSheetHistoryData="TimeSheetHistoryData"></RollBack>
    </section>
</template>

<script>
import Schema from '../../views/TimesheetApproval/TimesheetApprovalHistorySchema.js'
import DataService from '../../services/DataService';
import ShowComment from '../../components/Modal/Timesheet/ShowComment.vue';
import RollBack from '../../components/Modal/Timesheet/RollBack.vue';
export default {
    components: {
    ShowComment,
    RollBack
},
    data() {
        return {
            ShowRollBackModal:false,
            ShowCommentModal:false,
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            Headers: [{
                "COLUMN_NAME": 'TIME_SHEET_PERIOD',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TimesheetPeriod'),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": 'USERNAME',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UserName'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": 'TRACK_HOURS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TrackedHour'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'ISFORCIABLE',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('PerDiem'),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": 'STATUS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": 'Comment',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Comment'),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": 'RollBack',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Rollback'),
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true,
                }
            },

            ],
            noRecord: true,
            SortBy: '',
            SortExp: '',
            GroupData: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            SearchuserId:'',
            dateFrom:'',
            dateTo:'',
            statusCode:'',
            sortExp:'',
            sortBy:'',
            listheaderbuttons: Schema.buttons,
            DetailListType: 'List',
            ApplicationCategoryHistoryList:[],
            TimeSheetDate:'',
            TimeSheetHistoryData:''

        }
    },
    created() {
        var vm = this;
        Schema.bindleftCommonSearchdropdown(this, "CA", "USERS");
        vm.FetchData();
        vm.LegendArray = [{
            class: 'legend-pending colorboxsquare',
            text: ('The approved entries which have been rejected(Override) by CA')
        },
        {
            class: 'legends_medium colorboxsquare',
            text: ('The pending entries which have been approved/rejected directly by CA')
        },]
    },
    methods: { 
        async FetchData() {
            debugger;
            var vueObj = this;
            //vueObj.isLoading = true;
            //long companyId, long SearchuserId, string userType, DateTime? dtFrom, DateTime? dtTo, long? userId, string showData, string statusCode, string sortBy, string sortExp, int pageSize, int? pageNum, long? projectId
            if(vueObj.searchCondition == undefined){
                var url =`userId=${vueObj.SearchuserId}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}`
            }
            else{
                var url =`userId=${vueObj.SearchuserId}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&${vueObj.searchCondition}`
            }
           
           
            await DataService.TimesheetHistoryList(url).then(response => {
                debugger;
                if (response.data.length > 0) {
                    vueObj.ApplicationCategoryHistoryList = response.data;
                    vueObj.ApplicationCategoryHistoryList.forEach(item=>{
                        var getitem = vueObj.ApplicationCategoryHistoryList.find(x=> x.TRACK_HOURS === item.TRACK_HOURS);
                        getitem.TRACK_HOURS = vueObj.secondsToTime(item.TRACK_HOURS);
                    })
                    vueObj.isLoading = false;
                    vueObj.TotalRecords = response.data[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                    vueObj.CurrentPage = vueObj.pageNumber;
                    vueObj.noRecord = false;
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                }
                else {
                    vueObj.ApplicationCategoryHistoryList = [];
                    vueObj.noRecord=true
                }
                vueObj.isLoading = false;
            });
            // setTimeout(function () {
            //     vueObj.CheckBoxBootstrap();
            // }, 100);
        },      
        BackTolist() {
            this.$router.push({
                name: 'TimeApproval'
            });
        },
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        pagerMethod: function (value) {
            debugger;
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "BackTolist":
                    this.BackTolist();
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        CloseRollBack(){
                this.ShowRollBackModal = false;
            },
        CloseComment(){
                this.ShowCommentModal = false;
            },
        OpenCommentModal(value){
            debugger;
            this.TimeSheetDate = value;
            this.ShowCommentModal=true
        },
        OpenRollBackModal(data){
            debugger;
            this.ShowRollBackModal=true
            this.TimeSheetHistoryData = data
        },
        secondsToTime(secs){
            // TO Get Hours
            var hours = Math.floor(secs / (60 * 60));
            // To Get Minutes 
            var divisor_for_minutes = secs % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            // To Get Seconds
            var divisor_for_seconds = divisor_for_minutes % 60;
            var seconds = Math.ceil(divisor_for_seconds);

            var obj = hours + ':' + minutes ;
            return obj;
        },
    },

}
</script>
