import DataService  from '../../services/DataService'
let buttons =
[
    {
        title: 'Back to list',
        iconClass: 'fa fa-angle-double-left pr-2',
        callbackfunction: 'BackTolist',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: false
    },   
];
let leftsearchSchema = [
    {
        fieldName: 'Status',
        fieldType: "radio",
        fieldIcon: "fa fa-bar-chart",
        paramName:"statusCode",
        fieldSearchConditionName: [''],
        isSearch: false,
        value:"",
        listOptions: [
            {value:'TRACK_ENTRY_APPROVED',name:'Approved'},
        {value:'TRACK_ENTRY_REJECTED',name:'Rejected'}
        ]
    },
    {
        fieldName: 'From Date',
        fieldType: "date",
        fieldIcon: "fa fa-calendar",
        paramName:"dateFrom",
        fieldSearchConditionName: [''],
        isSearch: false,
        datePickerConfig: {
             useCurrent: true,
             showClear: true,
             showClose: true,
             minDate: true,
             maxDate: true,
             widgetPositioning: {
                 horizontal: 'right',
                 vertical: 'bottom'
             }
         },
         
        
    },
    {
        fieldName: "To Date",
        fieldType: "date",
        fieldIcon: "fa fa-calendar",
        paramName:"dateTo",
        fieldSearchConditionName: [''],
        isSearch: false,
        datePickerConfig: {
             useCurrent: true,
             showClear: true,
             showClose: true,
             minDate: true,
             maxDate: true,
             widgetPositioning: {
                 horizontal: 'right',
                 vertical: 'bottom'
             }
         },
        
    },
{
    fieldName: 'User',
    fieldType: "radio",
    fieldIcon: "fa fa-users",
    paramName:"SearchuserId",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: []
},]
function  bindleftCommonSearchdropdown(instance, filtername) {
    debugger;
    var vm = instance;
    if(filtername == 'CA'){
        DataService.GetUsersByCompanyId().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                vm.leftSearchFields[3].listOptions.push(obj1);
            })
        }).catch(err =>{
            console.log(err);
        })
    }
 }

export default { buttons, leftsearchSchema,bindleftCommonSearchdropdown};